import React from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import classNames from "classnames"

import "./Title.css"

const Title = ({ title }) => (
  <div className={classNames( 'row', 'title' )}>
    <Link to="/">
      {title}
    </Link>
  </div>
)

Title.propTypes = {
  title: PropTypes.string,
}

Title.defaultProps = {
  title: 'FATSBY',
}

export default Title
