import React from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import classNames from "classnames"

import "./FootMenu.css"

const FootMenu = ({ footLinks }) => (
  <div className={classNames( 'footMenu' )}>
    {
      footLinks.map(link =>
        <li className={classNames( 'footLink' )}>
          <Link to={link.link}>
                {link.name}
          </Link>
        </li>
      )
    }
  </div>
)

FootMenu.propTypes = {
  footLinks: PropTypes.arrayOf(PropTypes.object),
}

FootMenu.defaultProps = {
  menuLinks: [
    {
      name: 'Impressum',
      link: '/impressum',
    },
  ],
}

export default FootMenu
