import React from "react"
import PropTypes from "prop-types"
import classNames from "classnames"
import { StaticQuery, graphql } from "gatsby"
import BackgroundImage from 'gatsby-background-image'

import "./Contrast.css"

const RotatedContrast = ({ children }) => (
  <StaticQuery 
    query={graphql`
      query {
        rotatedContrast: file(relativePath: { eq: "Rost-Banner-Rotiert.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1360) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `}
    render={data => {
      const rotatedContrast = data.rotatedContrast.childImageSharp.fluid
      return (
        <BackgroundImage className={classNames( 'rotatedContrast' )} fluid={rotatedContrast}>
          {children}
        </BackgroundImage>
      )
    }}
  />
)

RotatedContrast.propTypes = {
  children: PropTypes.node.isRequired,
}

export default RotatedContrast