import React from "react"
import PropTypes from "prop-types"
import classNames from "classnames"

import CopyrightNote from "./CopyrightNote/CopyrightNote"
import FootMenu from "./FootMenu/FootMenu"

import "./Footer.css"

const Footer = ({ footLinks }) => (
  <footer className={classNames( 'row', 'siteWidth' )}>
    <CopyrightNote />
    <FootMenu footLinks={footLinks} />
  </footer>
)

Footer.propTypes = {
  footLinks: PropTypes.arrayOf(PropTypes.object),
}

export default Footer
