import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql, Link } from "gatsby"
import classNames from "classnames"
import BackgroundImage from 'gatsby-background-image'

import "./Menu.css"

const Menu = ({ menuLinks }) => (
  <StaticQuery 
    query={graphql`
      query {
        button: file(relativePath: { eq: "Rost-Button-Dunkel.jpg" }) {
          childImageSharp {
            fluid(maxHeight: 29) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        },
        hover: file(relativePath: { eq: "Rost-Button-Hell.jpg" }) {
          childImageSharp {
            fluid(maxHeight: 29) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `}
    render={data => {
      const button = data.button.childImageSharp.fluid
      const hover = data.hover.childImageSharp.fluid
      return (
        <nav className={classNames( 'row' )}>
          {
            menuLinks.map(link =>
              <BackgroundImage Tag="li" key={link.name} className={classNames( 'menuLink' )} fluid={button}>
                <Link to={link.link}>
                  <BackgroundImage className={classNames( 'menuHover' )} fluid={hover}>
                    <div className={classNames( 'menuOverlay' )}>
                      {link.name}
                    </div>
                  </BackgroundImage>
                </Link>
              </BackgroundImage>
            )
          }
        </nav>
      )
    }}
  />
)

Menu.propTypes = {
  menuLinks: PropTypes.arrayOf(PropTypes.object),
}

Menu.defaultProps = {
  menuLinks: [
    {
      name: 'Home',
      link: '/',
    },
    {
      name: 'Page 2',
      link: '/page-2',
    },
  ],
}

export default Menu