import React from "react"
import PropTypes from "prop-types"
import classNames from "classnames"

import Icon from "../Icon/Icon"
import Title from "./Title/Title"
import SubTitle from "./SubTitle/SubTitle"
import Menu from "./Menu/Menu"

import "./Header.css"

const Header = ({ title, subTitle, menuLinks }) => (
  <header className={classNames( 'row', 'siteWidth' )}>
    <Icon />
    <div style={{ width:'100%', display: 'flex', flexDirection: 'column' }}>
      <div className={classNames( 'aboveTitle' )} />
      <div className={classNames( 'row', 'spaceAround' )}>
        <div style={{ flexGrow:1.1 }}/>
        <Title title={title} />
        <SubTitle subTitle={subTitle} />
        <div style={{ flexGrow:3 }}/>
      </div>
      <div className={classNames( 'row' )}>
        <div style={{ flexGrow:1 }}/>
        <Menu menuLinks={menuLinks} />
        <div style={{ flexGrow:9.1 }}/>
      </div>
    </div>
  </header>
)

Header.propTypes = {
  title: PropTypes.string,
  subTitle: PropTypes.arrayOf(PropTypes.string),
  menuLinks: PropTypes.arrayOf(PropTypes.object),
}

export default Header
