import React from "react"
import classNames from "classnames"
import { StaticQuery, graphql, Link } from "gatsby"
import BackgroundImage from 'gatsby-background-image'
//import Img from "gatsby-image"

import "./Icon.css"

/*
 * This component is built using `gatsby-image` to automatically serve optimized
 * images with lazy loading and reduced file sizes. The image is loaded using a
 * `StaticQuery`, which allows us to load the image from directly within this
 * component, rather than having to pass the image data down from pages.
 *
 * For more information, see the docs:
 * - `gatsby-image`: https://gatsby.dev/gatsby-image
 * - `StaticQuery`: https://gatsby.dev/staticquery
 */

const Icon = () => (
  <StaticQuery
    query={graphql`
      query {
        icon: file(relativePath: { eq: "Pantoffelkrebs.jpg" }) {
          childImageSharp {
            fixed(quality: 100, width: 106) {
              ...GatsbyImageSharpFixed_withWebp
            }
          }
        }
      }
    `}
    render={data => {
      const icon = data.icon.childImageSharp.fixed
      return (
        <Link className={classNames( 'icon' )} to='/' >
          <BackgroundImage fixed={icon}>
            <div className={classNames( 'iconOverlay' )} />
          </BackgroundImage>
        </Link>
      )
    }}
  />
)
export default Icon
