import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"
import BackgroundImage from 'gatsby-background-image'

import "./Main.css"

const Main = ({ children }) => (
  <StaticQuery 
    query={graphql`
      query {
        mainBackground: file(relativePath: { eq: "Flechten-Hintergrund.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1360) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `}
    render={data => {
      const imageData = data.mainBackground.childImageSharp.fluid
      return (
        <BackgroundImage Tag="main" fluid={imageData}>
          {children}
        </BackgroundImage>
      )
    }}
  />
)

Main.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Main