import React from "react"
import PropTypes from "prop-types"
import classNames from "classnames"
import { StaticQuery, graphql } from "gatsby"
import BackgroundImage from 'gatsby-background-image'

import "./Contrast.css"

const Contrast = ({ children }) => (
  <StaticQuery 
    query={graphql`
      query {
        contrast: file(relativePath: { eq: "Rost-Banner.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1360) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `}
    render={data => {
      const contrast = data.contrast.childImageSharp.fluid
      return (
        <BackgroundImage className={classNames( 'contrast' )} fluid={contrast}>
          {children}
        </BackgroundImage>
      )
    }}
  />
)

Contrast.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Contrast